body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    background-color: #fff;
}


/* Thanh nav bar */
.navbar-top-space {
    height: 5vh;
    width: 100vw;
    display: block;
    top: 0;
    z-index: 999;
}

/* Khu vuc duoi thanh nav bar */
.header-space {
    width: 100vw;
    height: 30vh;
    padding-top: 15vh;
    padding-bottom: 15vh;
}

/* khu vuc cua service , product */
.items-space {
    width: 100vw;
    height: 120vh;
    display: block;
}

/* Khu vuc footer thong tin  */
.footer-space {
    width: 100vw;
    height: 70vh;
    display: block;
    background-color: #333;
    color: #fff;
}
.gioi-thieu-space {
    height: 75vh;
    width: 100vw;
    display: block;
    background-color: #fff;

}

.quy-trinh-lam-viec-space {
    height: 86vh;
    width: 100vw;
    display: block;
}

/* div booking */
.form-booking-space {
    width: 100vw;
    height: 100vh;
    display: block;
    background-color: #fff;
}
.div-list-items-sp {
    height: 50vh;
    width: 25vw;
    border: solid 0.5px;
}

.div-list-items-sp h4 {
    text-align: center;
    margin-top: 5px;
}

.div-list-items-sp p {
    margin-left: 5px;
}

.div-list-items-sp h5 {
    text-align: center;
    color: #FF8C00;
}

.div-list-items-sp img {
    width: 90%;
    height: 80%;
}
.div-list-items {
    height: 50vh;
    width: 27.2vw;
    border: solid 0.5px;
}

.div-list-items h4 {
    text-align: center;
    margin-top: 5px;
}

.div-list-items p {
    margin-left: 5px;
}
.gioi-thieu-space {
    height: 75vh;
    width: 100vw;
    display: block;
    background-color: #fff;

}

.gioi-thieu-space2 {
    height: 100vh;
    width: 100vw;
    display: block;
    background-color: #fff;

}
.items-space-km {
    width: 100vw;
    height: 80vh;
    display: block;
}

.div-list-items {
    height: 45vh;
    width: 30vw;
    border: solid 0.5px;
}

.div-list-items h4 {
    text-align: center;
    margin-top: 5px;
}

.div-list-items p {
    margin-left: 5px;
}

.div-list-items h5 {
    text-align: center;
    margin-top: 15px;
    margin-left: 5px;
    color: #FF8C00;
}

.div-list-items img {
    width: 100%;
    height: 50%;
}

.div-inform {
    display: flex;
}
